.search-container {
    padding: 10px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    height: 50px;
    background: #f4f4f4;
    font-size: 12px;
}

.search-container input {
    width: 200px;
    text-indent: 20px;
    background: #fff;
}

.search-container button {
    width: 60px;
    line-height: 8px;
}

.list {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: auto;
}

.item {
    position: relative;
    height: 55px;
    padding-left: 20px;
    border-bottom: 1px solid #e5e5e5;
}

.songname {
    height: 50%;
    line-height: 25px;
    font-size: 16px;
    font-weight: normal;
    color: #000;
}

.singer {
    height: 50%;
    line-height: 35px;
    font-size: 12px;
    color: #808080;
}
.play {
    position: absolute;
    top: 20px;
    right: 80px;
    font-size: 12px;
}
.download {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 12px;
}



